
import { defineComponent } from "vue"
import { ICM20200112_RES, ICM20200112_REQ_BODY, ContentList, AttachInfoList } from "@/shared/type/ICM20200112.type"
import { ICM20200141_RES, ICM20200141_REQ_BODY } from "@/shared/type/ICM20200141.type"
import { ICM20200121_RES, ICM20200121_REQ_BODY } from "@/shared/type/ICM20200121.type"
import { ICM20200131_RES, ICM20200131_REQ_BODY } from "@/shared/type/ICM20200131.type"
import { ModifyCardType } from "@/shared/components/ModifyCard/ModifyCard.type"
import { FILE, FILE_TYPE } from "@/shared/constants/common.const";
import { modalController } from "@ionic/vue"
import { FileSize } from "@/shared/services/FileSizeService"
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue'
import VueDatePicker from "@vuepic/vue-datepicker"
import baseTab from "@/shared/components/Tab/BaseTab.vue"
import basePan from "@/shared/components/Tab/BaseTabPan.vue"
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue"
import TextEditor from '@/shared/components/TextEditor/TextEditor.vue'
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue"
import { APM20200113_RES } from "@/shared/type/APM20200113.type"
import { screenMode } from "@/shared/type/Approval.type"
import { FileInfo } from "@/shared/type/ICM20100112.type"

const fileSize = new FileSize()

export default defineComponent({
    name: "icm202001sc01",

    components: {
        baseTab,
        basePan,
        VueDatePicker,
        ModifyCard,
        BreadCrumb,
        SelectOption,
        TextEditor
    },

    data() {
        return {
            boardFAQLangDefaultValue: [
                { languageCode: "common.english", title: "", faqQuestion: "", faqAnswer: "", faqAnswerHtml: '' },
                { languageCode: "common.khmer", title: "", faqQuestion: "", faqAnswer: "", faqAnswerHtml: '' },
                { languageCode: "common.korean", title: "", faqQuestion: "", faqAnswer: "", faqAnswerHtml: '' },
                { languageCode: "common.japanese", title: "", faqQuestion: "", faqAnswer: "", faqAnswerHtml: '' },
                { languageCode: "common.chinese", title: "", faqQuestion: "", faqAnswer: "", faqAnswerHtml: '' },
            ] as ContentList[],
            headersIcon: [
                { key: "En", name: "common.english" },
                { key: "Km", name: "common.khmer" },
                { key: "Ko", name: "common.korean" },
                // { key: "Ja", name: "common.japanese" },
                // { key: "Zh", name: "common.chinese" },
            ],
            faqList: {
                faqCategoryName: "",
                faqID: "",
                faqCategoryCode: "",
                attachInfoList: [] as AttachInfoList[],
                displayYN: "",
                contentList: [] as ContentList[],
                statusCode: '',
            },
            fileInfo: {} as FileInfo,
            modifyCard: {} as ModifyCardType,
            faqCategoryCodeList: [],
            isInvalid: false,
            isEdit: true,
            getFile: null,
            getImage: null,
            isFileEmty: false,
            isImageEmty: false,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            this.requestFaqcategoryCodeList()
            if (this.$route.query.type === "register") {
              this.$util.editableContent(true)
              this.isEdit = false
            } else {
              this.$util.editableContent(false)
              this.requestFaqDetail()
            }
        } else {
            await this.requestFaqcategoryCodeList();
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.$util.editableContent(false)
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const body = { faqID: Number(authDetailParsed.faqID) } as ICM20200112_REQ_BODY;
                    const res = await this.$request.getRequest("ICM20200112", body, false) as ICM20200112_RES;
                    await this.assignValueToTab(this.convertCommonCode(res?.body.contentList))
                    this.faqList = {
                        faqCategoryName: res?.body.faqCategoryName,
                        faqID: res?.body.faqID,
                        faqCategoryCode: res?.body.faqCategoryCode,
                        attachInfoList: res?.body.attachInfoList,
                        displayYN: res?.body.displayYN,
                        contentList: res?.body.contentList,
                        statusCode: res?.body.statusCode
                    }
                    this.requestFileDetail()
                } else {
                    await this.assignValueToTab(this.convertCommonCode(authDetailParsed.contentList))
                    this.faqList = {
                        faqCategoryName: authDetailParsed.faqCategoryName,
                        faqID: authDetailParsed.faqID,
                        faqCategoryCode: authDetailParsed.faqCategoryCode,
                        attachInfoList: authDetailParsed.attachInfoList,
                        displayYN: authDetailParsed.displayYN,
                        contentList: authDetailParsed.contentList,
                        statusCode: authDetailParsed.statusCode
                    }
                    this.requestFileDetail()
                }
            })
        }
    },

    watch: {
      boardFAQLangDefaultValue: {
        handler(value) {
          value.forEach((item: ContentList) => {
            item.faqAnswer = this.$validate.formatHtmlToString(item.faqAnswerHtml)
          })
        },
        deep: true
      }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        displayYN(): any {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },
        btnDelete(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnCancel(): boolean {
            if (!this.isEdit) {
                return true
            }
            return false
        },
        btnEdit(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnSave(): boolean {
            if ((this.$route.query.type == 'edit' && !this.isEdit) || (this.$route.query.type == 'register')) {
                return true
            }
            return false
        },
        btnList(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        }
    },

    methods: {
        async requestFaqcategoryCodeList() {
            const reqBody = { parentCategoryCode: '004' }
            const res: any = await this.$request.getRequest("ICM20000111", reqBody, false)
            this.faqCategoryCodeList = res.body.list
        },
        async requestFaqDetail() {
            const body = { faqID: Number(this.$route.query.faqID) } as ICM20200112_REQ_BODY
            const res = await this.$request.getRequest("ICM20200112", body, false) as ICM20200112_RES
            await this.assignValueToTab(this.convertCommonCode(res?.body.contentList))
            this.faqList = {
                faqCategoryName: res?.body.faqCategoryName,
                faqID: res?.body.faqID,
                faqCategoryCode: res?.body.faqCategoryCode,
                attachInfoList: res?.body.attachInfoList,
                displayYN: res?.body.displayYN,
                contentList: res?.body.contentList,
                statusCode: res?.body.statusCode
            }
            this.modifyCard = {
                registerTellerId: res?.body.firstRegisterTellerID,
                registerTellerName: res?.body.firstRegisterTellerName,
                registerDate: res?.body.firstRegisterDate,
                registerTime: res?.body.firstRegisterTime,
                updatedTellerId: this.faqList.statusCode === '09' ? "" : res?.body.lastChangeTellerID,
                updatedTellerName: this.faqList.statusCode === '09' ? "" : res?.body.lastChangeTellerName,
                updatedDate: this.faqList.statusCode === '09' ? "" : res?.body.lastChangeDate,
                updatedTime: this.faqList.statusCode === '09' ? "" : res?.body.lastChangeTime,
                deleteTellerId: this.faqList.statusCode === '09' ? res?.body.lastChangeTellerID : "",
                deleteTellerName: this.faqList.statusCode === '09' ? res?.body.lastChangeTellerName : "",
                deleteDate: this.faqList.statusCode === '09' ? res?.body.lastChangeDate : "",
                deleteTime: this.faqList.statusCode === '09' ? res?.body.lastChangeTime : "",
            }
            this.requestFileDetail()
        },

        async handleRegister() {
            await this.handleFileRegister()
                .then(async (res) => {
                    if(res === false) {
                        console.error('Error file server: please register or update without file!')
                    } else {
                        const reqBody = {
                            faqID: this.faqList.faqID,
                            displayYN: this.faqList.displayYN,
                            attachInfoList: this.faqList.attachInfoList,
                            faqCategoryCode: this.faqList.faqCategoryCode,
                            contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.boardFAQLangDefaultValue))),
                        } as ICM20200121_REQ_BODY
                        const res = await this.$request.getRequest("ICM20200121", reqBody, false) as ICM20200121_RES
                        if (res.header.result) {
                            this.$router.push("ICM202001SC00")
                            this.$dialog.onOpen({
                                content: this.$t("message.register_success"),
                                dialogMode: 'Success',
                                callback: () => {
                                    modalController.dismiss()
                                }
                            })
                        }
                    }
                })
        },

        async requestFileDetail() {
            for (let i = 0; i < this.faqList.attachInfoList.length; i++) {
                if (this.faqList.attachInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
                    const res = await this.$file.getFileInfo(this.faqList.attachInfoList[i].fileId)
                    this.fileInfo.fileName = res?.data.body.originalFileName
                    this.fileInfo.fileSize = res?.data.body.fileSizeString
                }
                if (this.faqList.attachInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
                    const res = await this.$file.getFileInfo(this.faqList.attachInfoList[i].fileId)
                    this.fileInfo.imageName = res?.data.body.originalFileName
                    this.fileInfo.imgSize = res?.data.body.fileSizeString
                    const resImage = await this.$file.previewImage(this.faqList.attachInfoList[i].fileId)
                    this.fileInfo.image = resImage
                }
            }
        },

        async handleUpdate() {
            await this.handleFileUpdate()
                .then(async (res) => {
                    if(res === false) {
                        console.error('Error file server: please register or update without file!')
                    } else {
                        const reqBody = {
                            displayYN: this.faqList.displayYN,
                            attachInfoList: this.faqList.attachInfoList,
                            faqID: Number(this.$route.query.faqID),
                            faqCategoryCode: this.faqList.faqCategoryCode,
                            contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.boardFAQLangDefaultValue))),
                        } as ICM20200131_REQ_BODY
                        const res = await this.$request.getRequest("ICM20200131", reqBody, false) as ICM20200131_RES

                        if (res?.header.result) {
                            this.isEdit = true
                            this.$util.editableContent(false)
                            this.$dialog.onOpen({
                                content: this.$t("message.update_success"),
                                dialogMode: 'Success',
                                callback: () => {
                                    this.requestFaqDetail()
                                    modalController.dismiss()
                                }
                            })
                        }
                    }
                })
        },

        async handleDelete() {
            const reqBody = { faqID: Number(this.$route.query.faqID) } as ICM20200141_REQ_BODY
            const res = await this.$request.getRequest("ICM20200141", reqBody, false) as ICM20200141_RES
            // this.faqList.attachInfoList.forEach((item) => { this.$file.updateFileStatus(item.fileId, FILE_TYPE.INACTIVE) })
            if (res?.header.result) {
                this.$router.push("ICM202001SC00")
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                })
            }
        },

        handleClickAction(buttonType: string) {
            switch (buttonType) {
                case "delete":
                    if (this.$route.query.type == "edit") {
                        this.$dialog.onOpen({
                            content: this.$t("message.are_you_sure_you_want_to_delete"),
                            dialogMode: 'Question',
                            btnLabel: this.$t("common.confirm"),
                            callback: () => {
                                this.handleDelete()
                                modalController.dismiss()
                            },
                        })
                    } else {
                        this.$router.push("ICM202001SC00")
                    }
                    break
                case "cancel":
                    this.$dialog.onOpen({
                        dialogMode: 'Question',
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        callback: () => {
                            if (this.$route.query.type == "edit") {
                                this.isEdit = true
                                this.$util.editableContent(false)
                                this.requestFaqDetail()
                            } else {
                                this.$router.push("ICM202001SC00")
                            }
                            modalController.dismiss()
                        },
                    })
                    break
                case "save":
                    var item: any = this.boardFAQLangDefaultValue.find(data => data.languageCode == 'common.english')
                    if (item.faqAnswer && item.faqQuestion && item.title && this.faqList.faqCategoryCode) {
                        this.$dialog.onOpen({
                            dialogMode: 'Question',
                            content: this.$t("message.would_you_like_to_save"),
                            callback: () => {
                                if (this.$route.query.type == "edit") {
                                    this.handleUpdate()
                                } else {
                                    this.handleRegister()
                                }
                                modalController.dismiss()
                            },
                        })
                    } else {
                        this.isInvalid = true
                        this.$util.scrollToElement('inp_error')
                    }
                    break
                case "edit":
                    this.isEdit = false
                    this.$util.editableContent(true)
                    break
                default: //list case
                    this.$router.push("ICM202001SC00")
                    break
            }
        },

        convertCommonCode(listArr: ContentList[]): ContentList[] {
            const convertLangCode = listArr.map((item: ContentList) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : ""
                return item
            })
            return convertLangCode
        },

        convertLangToCode(listsArr: ContentList[]): ContentList[] {
            const data = listsArr.map((item: ContentList) => {
                item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value")
                return item
            })
            return data
        },

        checkAttachInfo(attachFile: any) {
            return {
                attachTypeCode: attachFile.attachTypeCode,
                boardFileBizTypeCode: attachFile.boardFileBizTypeCode,
                fileId: attachFile.fileId,
                filePath: attachFile.filePath,
                size: attachFile["size"],
            }
        },

        removeFile(attachTypeCode: string) {
            switch (attachTypeCode) {
                case FILE_TYPE.FILE:
                    this.isFileEmty = true
                    this.getFile = null
                    this.fileInfo.fileSize = this.fileInfo.fileName = '';
                    break;
                case FILE_TYPE.IMAGE:
                    this.isImageEmty = true
                    this.getImage = null
                    this.fileInfo.imageName = this.fileInfo.imgSize = '';
                    break;
            }
        },

        handleUploadFile(event: any) {
            this.isFileEmty = false
            const files = event.target.files
            const file = files[0]
            if (file.size > FILE.SIZE_10MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_10MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getFile = file
                const size = fileSize.countFileSize(file.size)
                this.fileInfo.fileSize = size
                const reader = new FileReader()
                reader.readAsDataURL(file)
                this.fileInfo.fileName = file.name
            }
            const duplicatedFile = document.getElementById('actual-btn') as HTMLInputElement;
            duplicatedFile.value = ''
        },

        handleUploadImage(event: any) {
            this.isImageEmty = false
            const accectFile = /(\.png)$/i
            const files = event.target.files
            const file = files[0]
            if (!accectFile.exec(file.name)) {
                this.$dialog.onOpen({
                    content: this.$t('message.only_png_file_can_be_inserted'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss()
                    },
                })
            } else if (file.size > FILE.SIZE_2MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_2MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss()
                    },
                })
            } else {
                this.getImage = file
                const size = fileSize.countFileSize(file.size)
                this.fileInfo.imgSize = size
                const reader = new FileReader()
                reader.readAsDataURL(file)
                this.fileInfo.imageName = file.name
                reader.onload = () => {
                    this.fileInfo.image = String(reader.result)
                }
            }
            const duplicatedFile = document.getElementById('btn02 btn_upload') as HTMLInputElement;
            duplicatedFile.value = ''
        },

        async handleFileUpdate(): Promise<boolean | void> {
      if (this.getFile !== null && this.getImage !== null && !this.isFileEmty && !this.isImageEmty) {
        if (this.faqList.attachInfoList) {
          this.faqList.attachInfoList.forEach(async (item) => {
            await this.$file.updateFileStatus(item.fileId, FILE_TYPE.INACTIVE)
          })
        }
        this.faqList.attachInfoList = []
        const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            if (res.data.body.fileList[i].keyName === "file1") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.faqList.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.FILE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
            if (res.data.body.fileList[i].keyName === "file2") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.faqList.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.IMAGE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          }
        } else {
          return false
        }
      }
      if (this.getFile === null && this.isFileEmty) {
        for (let i = 0; i < this.faqList.attachInfoList.length; i++) {
          if (this.faqList.attachInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
            const res = await this.$file.updateFileStatus(this.faqList.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
            if (res) {
              const index = this.faqList.attachInfoList.indexOf(this.faqList.attachInfoList[i]);
              this.faqList.attachInfoList.splice(index, 1);
            } else {
              return false
            }
          }
        }
      }
      if (this.getImage === null && this.isImageEmty) {
        for (let i = 0; i < this.faqList.attachInfoList.length; i++) {
          if (this.faqList.attachInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
            const res = await this.$file.updateFileStatus(this.faqList.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
            if (res) {
              const index = this.faqList.attachInfoList.indexOf(this.faqList.attachInfoList[i]);
              this.faqList.attachInfoList.splice(index, 1);
            } else {
              return false
            }
          }
        }
      }
      if (this.getFile !== null && !this.isFileEmty) {
        if (this.faqList.attachInfoList.length === 0) {
          const res = await this.$file.uploadFile([this.getFile], "ICM")
          if (res) {
            for (let i = 0; i < res.data.body.fileList.length; i++) {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.faqList.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.FILE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          } else {
            return false
          }
        } else if (this.faqList.attachInfoList.length > 0) {
          if (this.isFileAlreadyExisted(FILE_TYPE.FILE)) {
            const res = await this.$file.uploadFile([this.getFile], "ICM")
            if (res) {
              for (let index = 0; index < this.faqList.attachInfoList.length; index++) {
                if (this.faqList.attachInfoList[index].attachTypeCode === FILE_TYPE.FILE) {
                  await this.$file.updateFileStatus(this.faqList.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                  for (let i = 0; i < res.data.body.fileList.length; i++) {
                    await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                      Object.assign(this.faqList.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.FILE, filePath: v.data.body.filePath })
                    )
                  }
                }
              }
            } else {
              return false
            }
          } else {
            const res = await this.$file.uploadFile([this.getFile], "ICM")
            if (res) {
              for (let i = 0; i < res.data.body.fileList.length; i++) {
                const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                this.faqList.attachInfoList.push({
                  filePath: resFileInfo.data.body.filePath,
                  attachTypeCode: FILE_TYPE.FILE,
                  fileId: res.data.body.fileList[i].fileId
                })
              }
            } else {
              return false
            }
          }
        }
      }
      if (this.getImage !== null && !this.isImageEmty) {
        if (this.faqList.attachInfoList.length === 0) {
          const res = await this.$file.uploadFile([this.getImage], "ICM")
          if (res) {
            for (let i = 0; i < res.data.body.fileList.length; i++) {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.faqList.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.IMAGE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          } else {
            return false
          }
        } else if (this.faqList.attachInfoList.length > 0) {
          if (this.isFileAlreadyExisted(FILE_TYPE.IMAGE)) {
            const res = await this.$file.uploadFile([this.getImage], "ICM")
            if (res) {
              for (let index = 0; index < this.faqList.attachInfoList.length; index++) {
                if (this.faqList.attachInfoList[index].attachTypeCode === FILE_TYPE.IMAGE) {
                  await this.$file.updateFileStatus(this.faqList.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                  for (let i = 0; i < res.data.body.fileList.length; i++) {
                    await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                      Object.assign(this.faqList.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.IMAGE, filePath: v.data.body.filePath })
                    )
                  }
                }
              }
            } else {
              return false
            }
          } else {
            const res = await this.$file.uploadFile([this.getImage], "ICM")
            if (res) {
              for (let i = 0; i < res.data.body.fileList.length; i++) {
                const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                this.faqList.attachInfoList.push({
                  filePath: resFileInfo.data.body.filePath,
                  attachTypeCode: FILE_TYPE.IMAGE,
                  fileId: res.data.body.fileList[i].fileId
                })
              }
            } else {
              return false
            }
          }
        }
      }
    },

    async handleFileRegister(): Promise<boolean | void> {
      if (this.getFile !== null && this.getImage !== null) {
        const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            if (res.data.body.fileList[i].keyName === "file1") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.faqList.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.FILE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
            if (res.data.body.fileList[i].keyName === "file2") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.faqList.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.IMAGE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          }
        } else {
          return false
        }
      } else if (this.getFile !== null && this.getImage === null) {
        const res = await this.$file.uploadFile([this.getFile], 'ICM')
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.faqList.attachInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.FILE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        } else {
          return false
        }
      } else if (this.getFile === null && this.getImage !== null) {
        const res = await this.$file.uploadFile([this.getImage], 'ICM')
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.faqList.attachInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.IMAGE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        } else {
          return false
        }
      }
    },

        isFileAlreadyExisted(fileContentType: FILE_TYPE.FILE | FILE_TYPE.IMAGE) {
            const isFileAlreadyExisted = this.faqList.attachInfoList.some(obj => Object.values(obj).includes(fileContentType))
            return isFileAlreadyExisted
        },

        async assignValueToTab(contentList) {
            for (let i = 0; i < contentList.length; i++) {
                this.boardFAQLangDefaultValue.map((item) => {
                    if (item.languageCode === contentList[i].languageCode) {
                      item.title = contentList[i].title;
                      item.faqAnswer = contentList[i].faqAnswer;
                      item.faqQuestion = contentList[i].faqQuestion;
                      item.faqAnswerHtml = contentList[i].faqAnswerHtml;
                    } else if (item.faqAnswerHtml === '') {
                      item.faqAnswerHtml = '<p><br></p>'
                    }
                })
            }
        },
    },
})
